
  .table {
    width: 100%;
  }
  
  .tableContainer th {
    border-left: none;
    position: sticky;
    top: 0;
    color: #2a5067;
    font-family: Inter;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 20px;
    background-color: #e2f8fb;
    gap: 10px;
    height: 48px;
  }
  
  .tableContainer td {
    border-left: none;
    border-bottom: 1px solid #e8e9e9;
    padding: 5px;
    font-family: Inter;
    font-size: 12px;
    font-style: normal;
    font-weight: 600;
    line-height: 18px;
    
  }
  
  .tableContainer {
    // width: 678px;
    overflow: auto;
    border: 1px solid;
    border-color: #e8e9e9;
    border-radius: 16px;
  }
  
  .tableContainer table {
    border-spacing: 0;
  }

  .tableContainer th:nth-child(1) {
    padding: 14px 21px;
  }
  
  .container {
    width: 1252px;
    margin-top: 24px;
    background-color: #E2F8FB;
    border-radius: 16px;
  }
  

  .tableContainer td:nth-child(3) {
   
    padding: 14px 8px;
    // background: var(--accent-c-109, #F3F5E7);
  }
  
  .dropdownArrow {
    margin: 10px;

  cursor: pointer;
  }
  .dropdownTable {
    background: var(--primary-10, #FAFBFB);
  }
  .color {
    background: var(--accent-c-109, #F3F5E7);
  }
  .inputfield {
    border: none;
    background: var(--accent-c-109, #F3F5E7);
  }
  .inputBorder{
    border: none;
    outline: none;
    background: var(--primary-10, #FAFBFB);
  }